<template>
  <div>
    <textarea :id="editorId" autofocus></textarea>
    <div class="container typo">
      <div v-html="text"></div>
    </div>
  </div>
</template>
<style lang="less" scoped>

</style>
<script>
  import $ from 'jquery'
  import '../../../node_modules/simditor/styles/simditor.scss'
  import Simditor from 'simditor'
  export default {
    data () {
      return {
        text: '',
        // 支持多个富文本
        editorId: new Date().getTime(),
        editor: ''
      }
    },
    mounted () {
      this.createEditor()
    },
    methods: {
      createEditor () {
        let self = this
        // 构造器必须大写
        self.editor = new Simditor({
          textarea: $(`#${self.editorId}`),
          placeholder: 'Confetti7',
          defaultImage: '',
          params: {},
          upload: false,
          tabIndent: true,
          toolbar: [
            'title',
            'bold',
            'italic',
            'underline',
            'strikethrough',
            'fontScale',
            'color',
            'ol',
            'ul',
            'blockquote',
            'code',
            'table',
            'link',
            'image',
            'hr',
            'indent',
            'outdent',
            'alignment'
          ],
          toolbarFloat: true,
          toolbarFloatOffset: 0,
          toolbarHidden: false,
          pasteImage: false,
          cleanPaste: false
        })
        // 传参说明 可以传的e/src才传
        // 当内容发生更改时，事件触发 src为触发事件
        self.editor.on('valuechanged', (e, src) => {
          self.text = self.editor.getValue()
        })
        // 当光标位置或选定文本发生更改时，事件触发
        // self.editor.on('selectionchanged', (e) => {})
        // 在粘贴内容和调用setValue时触发事件
        // self.editor.on('decorate', (e, $el) => {})
        // 将内容粘贴到编辑器时触发事件。
        // Param $ pasteContent是粘贴的内容。你可以有效的内容和停止粘贴。
        // self.editor.on('pasting', (e, $pasteContent) => {})
        // 文本获取焦点时
        // self.editor.on('focus', (e) => {})
        // 文本失去焦点时
        // self.editor.on('blur', (e) => {})
        // 销毁时
        // self.editor.on('destroy ', (e) => {})
      }
    }
  }
</script>



