// typeof是使用最频繁的类型检测手段
//
// typeof 3        // "number"
// typeof "333"    // "string"
// typeof false    // "boolean"
//
// 对于基本（简单）数据类型还是挺好的，但是一旦到了引用数据类型的时候，就不那么好使了
//
// typeof new Date()   // "object"
// typeof []           // "object"
// typeof {}           // "object"
// typeof null         // "object"
import _ from 'lodash'
function _typeof (param) {
  let type = Object.prototype.toString.call(param)
  // 去掉[]
  return type.substr(1, type.length - 2).substr(7)
}
function formatTree (list, pkey) {
    var start = Date.parse(new Date())
    // 生成查找表，可以按 id 查到节点 一个元素对应着它的id 通过id可以访问到该元素
    const dict = list.reduce((all, item) => {
        all[item.id] = item;
        return all;
    }, {});

    // 默认已pid作为关键字，有可能是parentid等等
    var pkey = pkey || 'pid'

    // 由于原始数据没有 id 为 0 的根节点，
    // 这里模拟一个，最终它的 children 就是实际的所有根节点
    var root = {
        id: 0
    };
    dict[0] = root;
    
    // 循环添加关系
    list.forEach(item => {
        const parent = dict[item[pkey]];
        // 确保父节点的 children 存在
        parent.children = parent.children || [];
        parent.children.push(item);
    });
    
    var end = Date.parse(new Date())
    console.log('数据格式转换耗时:' + (end - start))
    // 输出结果 root.children
    return JSON.stringify(root.children)
}
export {
  _typeof,
  formatTree
}



