<template>
  <div class="article">
    <c-header></c-header>
    <!--动态绑定组件-->
    <component v-bind:is="article">
    </component>
  </div>
</template>
<style lang="less" scoped>
  .article {
    min-height: 500px;
  }
</style>
<script>
  import cHeader from '@/components/common/header'
  import editor from '@/components/common/editor'
  import article1 from '@/components/article/1'
  import {formatTree} from '@/util/common'
  export default {
    data () {
      return {
        article: 'editor'
      }
    },
    components: {
      cHeader,
      editor,
      article1
    },
    mounted () {
      this.turnArticle()
    },
    methods: {
      turnArticle () {
        let href = window.location.href
        if (href.indexOf('article/') > -1) {
          let index = href.split('article/')[1]
          this.article = 'article' + index
        } else {
          this.article = 'editor'
        }
      }
    },
    watch:{
      $route() {
        this.turnArticle()
      }
    }
  }
</script>



