<template>
  <div class="box-chat">
    <!--测试一下自定义属性-->
    <div id="newTest" myAttr="getAttr"></div>
    <div class="chat">
      <div v-for="(info, index) in infoList" class="info">
        <div v-if="info.fromRobot" class="robot-say say">
          <span class="positionTop7"><Icon type="social-tux" size="30"></Icon></span>
          <p>
            {{info.content}}
            <i></i>
          </p>
        </div>
        <div v-else="" class="you-say say">
          <p>
            {{info.content}}
            <i></i>
          </p>
          <span class="positionTop5">
             <Icon type="social-freebsd-devil" size="30"></Icon>
          </span>
        </div>
      </div>
      <div class="chat-footer"></div>
    </div>
    <div class="submit">
      <form @submit.prevent>
        <Input v-model="yourWord" placeholder="请输入..." style="width: 70%;"></Input>
        <Button html-type="submit" type="info" style="width: 15%;" @click="say">发送</Button>
      </form>
    </div>
  </div>
</template>
<style lang="less" scoped>
   .box-chat {
     height: 300px;
     width: 100%;
     background-color: #fff;
     /*border-radius: 10px;*/
     position: absolute;
     bottom: 0;
     .chat {
       height: 230px;
       padding-top: 10px;
       width: 100%;
       border-bottom: #f0f0f0 solid 1px;
       margin-bottom: 17px;
       overflow-y: scroll;
       .say {
         p {
           display: inline-block;
           word-break: break-all;
           margin-left: 5px;
           color: #333;
           font-size: 14px;
           padding: 3px 18px;
           line-height: 24px;
           background: #f1f1f1;
           -ms-border-radius: 6px;
           -moz-border-radius: 6px;
           -webkit-border-radius: 6px;
           -o-border-radius: 6px;
           border-radius: 6px;
           position: relative;
           max-width: 220px;
           i {
             position: absolute;
             width: 0;
             height: 0;
             bottom: 10px;
           }
         }
       }
       .robot-say {
         padding-left: 10px;
         text-align: left;
         p {
           i {
             border-top: 6px solid transparent;
             border-bottom: 6px solid transparent;
             border-right: 6px solid #f1f1f1;
             left: -6px;
           }
         }
       }
       .you-say {
         padding-right: 10px;
         text-align: right;
         p {
           background-color: #C6E2FF;
           margin-right: 4px;
           margin-left: 0;
           i {
             border-top: 6px solid transparent;
             border-bottom: 6px solid transparent;
             border-left: 6px solid #C6E2FF;
             right: -5px;
             top: 8px;
           }
         }
       }
     }
     .submit {
       text-align: center;
     }
     &::-webkit-scrollbar {
       width: 7px;
       /* background-color: #F5F5F5; */
     }
     /*滚动条的样式*/
     &::-webkit-scrollbar-thumb {
       background-color: #E9E9E9;
       /*background-image: -webkit-gradient(linear, 40% 0%, 75% 84%, from(rgb(77, 156, 65)), color-stop(0.6, rgb(84, 222, 93)), to(rgb(25, 145, 29)));*/
       border-radius: 10px;
     }
     /*轨道的样式*/
     &::-webkit-scrollbar-track {
       -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
       background-color: #F5F5F5;
       /*border-radius: 10px;*/
     }
   }
   .chat > div {
     margin-top: 5px;
   }
   .positionTop7 {
     position: relative;
     top: 7px;
   }
   .positionTop5 {
     position: relative;
     top: 5px;
   }
</style>
<script>
  import vue from 'vue'
  import {mixin} from '@/mixin/index.js'
  export default {
//  测试一下混合模式 不会用( ⊙ o ⊙ )！
    mixins: [mixin],
    mounted () {
      this.foo()
      console.log(this.$root)
    },
    data () {
      return {
        infoList: [
          {
            fromRobot: true,
            content: '来耍我呀'
          },
          {
            fromRobot: false,
            content: 'who are you'
          }
        ],
        yourWord: ''
      }
    },
    methods: {
      say () {
        let word = {
          fromRobot: false,
          content: this.yourWord
        }
        this.infoList.push(word)
        let askWord = {
          'key': 'f4214de80e584796b244280ffa563253',
          'info': this.yourWord,
          'userid': '1'
        }
        askWord = JSON.stringify(askWord)
        this.yourWord = ''
        this.$http.post('/openapi/api', askWord).then((response) => {
          if (response.status === 200) {
            let word = {
              fromRobot: true,
              content: response.body.text
            }
            this.infoList.push(word)
          }
        })
      }
    },
    watch: {
      infoList () {
        vue.nextTick(function () {
          let chat = document.querySelector('.chat-footer')
          chat.scrollIntoView(true)
        })
      }
    }
  }
</script>



